<template>
  <div>
    <!-- 头部导航 -->
    <van-nav-bar
      title="找回密码"
      left-text=""
      left-arrow
      @click-left="onClickLeft"
    />

    <!-- 输入框密码 -->
    <van-form>
      <div class="lwh_input" v-for="(item, index) in lwh_value" :key="index">
        <input
          :class="{ lwh_valueIndex: index == lwh_valueIndex }"
          :type="item.txt"
          v-model="item.cont"
          :placeholder="item.place"
          @focus="lwh_focus(index)"
          @blur="lwh_blur()"
        />
        <span v-if="index == 0 && lwh_flag" @click="lwh_span_veify()"
          >获取验证码</span
        >
        <p v-if="index == 0 && !lwh_flag">获取验证码({{ lwh_time }})</p>
      </div>

      <div style="margin: 16px">
        <van-button
          round
          block
          type="info"
          native-type="submit"
          color="linear-gradient(to right, rgb(255, 146, 71),rgb(253, 88, 3))"
        >
          确认
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      lwh_value: [
        { cont: "", txt: "text", type: "name", place: "请输入手机号" },
        { cont: "", txt: "text", type: "pass", place: "请输入短信验证码" },
        { cont: "", txt: "password", type: "pass", place: "请输入密码" },
      ],
      lwh_valueIndex: null,
      lwh_flag: true,
      lwh_time: 59,
      acs:null,
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        path: "/Wyl-Login",
      });
    },
    // 获取焦点
    lwh_focus(index) {
      this.lwh_valueIndex = index;
    },
    // 失去焦点
    lwh_blur() {
      this.lwh_valueIndex = null;
    },
    // 获取验证码
    async lwh_span_veify() {
      console.log(
        /^1[3456789]d{9}$/.test(this.lwh_value[0].cont),
        this.lwh_value[0].cont
      );
      if (!(/^1[3456789]d{9}$/.test(this.lwh_value[0].cont))) {
        Toast("手机号格式错误");
        return false;
      }
      
      this.acs = setInterval(() => {
        this.lwh_time--;
        if (this.lwh_time == 0) {
          this.lwh_time = 59;
          this.lwh_flag = !this.lwh_flag;
          clearInterval(this.acs)
          
        }
      }, 1000);
      this.lwh_flag = !this.lwh_flag;
      let { data } = await this.$http.post("/api/app/smsCode", {
        mobile: this.lwh_value[0].cont,
        sms_type: "getPassword",
      });
    },
    //修改密码
    async lwh_login() {
      let { data } = await this.$http.post("/api/app/password", {
        mobile: this.lwh_value[0].cont,
        password: this.lwh_value[3].cont,
        sms_code: this.lwh_value[1].cont,
      });

      if (data.code != 200) return Toast(data.msg);

      Toast(data.msg);
      this.$router.push("/Wyl-Login");
      window.sessionStorage.setItem("token", data.data.remember_token);
    },
  },
};
</script>

<style scoped lang="scss">
.van-button {
  width: 3.14rem;
  margin-top: 0.6rem;
  margin-left: 0.4rem;
}
.lwh_input {
  width: 3.6rem;
  height: 0.52rem;
  margin-left: 0.2rem;
  margin-top: 0.02rem;
  position: relative;
  > input {
    width: 100%;
    border: none;
    border-bottom: 0.01rem solid #f1f1f1;
    height: 100%;
  }
  > .lwh_valueIndex {
    border-bottom: 0.01rem solid #eb6100;
  }
  > input::placeholder {
    color: #b7b7b7;
  }
  > span {
    font-size: 0.13rem;
    color: #eb6100;
    position: absolute;
    top: 0.17rem;
    right: 0.05rem;
  }
  > p {
    font-size: 0.13rem;
    color: rgb(204, 204, 204);
    position: absolute;
    top: 0.17rem;
    right: 0.05rem;
  }
}
</style>